<template>
  <el-table
    :data="cEstimateDetailsTableData"
    border
    :summary-method="getSummaries"
    show-summary
    v-loading="loading"
    size="mini"
    :header-cell-style="{'text-align':'center'}"
    :cell-style="{'text-align':'right'}"
    style="width: 100%; margin-top: 20px">
    <template slot="empty">
      <slot name="empty"></slot>
    </template>
    <el-table-column
      type="index"
      label="No."
      width="100"
      align="center">
    </el-table-column>
    <el-table-column
      prop="ProductName"
      label="商品名">
    </el-table-column>
    <el-table-column
      prop="Quantity"
      label="数量"
      width="80"
      align="right">
    </el-table-column>
    <el-table-column
      prop="Price"
      label="単価（円）"
      :formatter="stateFormat"
      align="right">
    </el-table-column>
    <el-table-column
      prop="Tax"
      label="消費税（円）"
      :formatter="stateFormat"
      align="right">
    </el-table-column>
    <el-table-column
      prop="Total"
      label="税込金額（円）"
      :formatter="stateFormat"
      align="right">
    </el-table-column>
    <el-table-column label="操作" width="80">
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          plain
          slot="reference"
          @click="handleDelete(scope.$index, scope.row)">删除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import {postEstimateDetailsData} from 'api/estimates'

export default {
  name: 'EstimateDetailsTable',
  props: {
    cEstimateDetailsTableData: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      loading: false
    }
  },
  methods: {
    getSummaries (param) {
      const {columns, data} = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '総計'
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          if (index === 2) {
            sums[index] = ''
          } else {
            sums[index] = (sums[index]).toString().replace(/(\d)(?=(\d{3})+$)/g, '$1,')
            sums[index] += '円'
          }
        } else {
          sums[index] = ''
        }
      })
      return sums
    },
    handleDelete (index, row) {
      this.loading = true
      let values = {}
      values['EstimateDetCode'] = row.EstimateDetCode
      values['Is_Delete'] = 1
      postEstimateDetailsData(values).then(res => {
        let code = res.data.code
        if (code === '200') {
          this.loading = false
          // 向父组件发送重新获取数据请求
          this.$emit('estimatedetdatareload')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    stateFormat (row, column, cellValue) {
      cellValue += ''
      if (!cellValue.includes('.')) cellValue += '.'
      return cellValue.replace(/(\d)(?=(\d{3})+\.)/g, function ($0, $1) {
        return $1 + ','
      }).replace(/\.$/, '')
    }
  }
}
</script>

